@font-face {
  font-family: 'FontAwesome';
  src: url('@{icon-font-path}/fontawesome-webfont.eot?v=@{version}');
  src: url('@{icon-font-path}/fontawesome-webfont.eot?#iefix&v=@{version}') format('embedded-opentype'),
    url('@{icon-font-path}/fontawesome-webfont.woff2?v=@{version}') format('woff2'),
    url('@{icon-font-path}/fontawesome-webfont.woff?v=@{version}') format('woff'),
    url('@{icon-font-path}/fontawesome-webfont.ttf?v=@{version}') format('truetype'),
    url('@{icon-font-path}/fontawesome-webfont.svg?v=@{version}#fontawesomeregular') format('svg');
  font-weight: normal;
  font-style: normal;
}

.icon {
  display: inline-block;
  font: normal normal normal @font-size-base/@line-height-base FontAwesome; // shortening font declaration
  font-size: inherit; // can't have font-size inherit on line above, so need to override
  text-rendering: auto; // optimizelegibility throws things off #1094
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

